import React, { Component } from 'react'
import { getCityList, getHotCity } from '../../utils/api/city'
import { getCurCity } from '../../utils/index';
// 
import { AutoSizer, List } from 'react-virtualized';
// 
import './index.scss'
import { NavBar, Icon, Toast } from 'antd-mobile';
import { setLocalData, CURR_CITY } from '../../utils/index'

// 
// const list = Array.from(new Array(100)).map((item, index) => index)
/* 
    
*/
class CityList extends Component {
    // 
    state = {
        // 
        cityList: {},
        // 
        cityIndex: [],
        // 
        activeIndex: 0
    }
    componentDidMount() {
        this.getCityData()
    }
    // 
    getCityData = async () => {
        const { status, data } = await getCityList()
        if (status === 200) {
            // console.log('', data);
            const { cityList,
                cityIndex } = this.formatCities(data);
            // console.log('', cityList, cityIndex)
            // =
            const { data: dt, status: st } = await getHotCity()
            // console.log('', res)
            if (st === 200) {
                cityList['hot'] = dt
                cityIndex.unshift('hot')
            }
            // console.log(cityList)
            // 
            const curCity = await getCurCity()
            cityList['#'] = [curCity]
            cityIndex.unshift('#')
            console.log(cityList);
            // 
            this.setState({
                cityList,
                cityIndex
            })

        }
    }


    // 
    // 1. formatCities
    // 2. 
    //    * =cityList = {}
    //    * =cityIndex=[]
    // 3. =****
    // 4. Object.keys(cityList)****
    formatCities = (data) => {
        let cityList = {}, cityIndex = [];
        data.forEach((item) => {
            // debugger
            // 
            // 
            let firstLetter = item.short.slice(0, 1);
            // 
            if (!cityList[firstLetter]) {
                // =
                cityList[firstLetter] = [item]
            } else {
                // 
                cityList[firstLetter].push(item)
            }
        })
        // 
        cityIndex = Object.keys(cityList).sort()
        // cityIndex =cityList['b']
        return {
            cityList,
            cityIndex
        }
    }
    // title
    formTitle = (title, isRight) => {
        switch (title) {
            case '#':
                return isRight ? '' : '';
            case 'hot':
                return isRight ? '' : '';
            default:
                return title.toUpperCase()

        }
    }
    // row
    /* 
        index 
    */
    execHeight = ({ index }) => {
        // 
        const { cityIndex, cityList } = this.state;
        // title
        const title = cityIndex[index]
        // title
        const titleCity = cityList[title]
        return 36 + 50 * titleCity.length
    }
    // 
    selCity = (item) => {
        // console.log(item)
        const hasData = ['', '', '', ''];
        if (hasData.includes(item.label)) {
            // 
            // 
            setLocalData(CURR_CITY, JSON.stringify(item))
            // 
            this.props.history.goBack()
        } else {
            // 
            Toast('', 2)
        }
    }
    // 
    rowRenderer = ({
        key, // Unique key within array of rows
        index, // Index of row within collection
        isScrolling, // The List is currently being scrolled
        isVisible, // This row is visible within the List (eg it is not an overscanned row)
        style, // Style object to be applied to row (to position it)
    }) => {
        // 
        const { cityIndex, cityList } = this.state;
        // title
        const title = cityIndex[index]
        // title
        const titleCity = cityList[title]
        return (
            <div key={key} style={style} className="city-item">
                <div className="title">{this.formTitle(title)}</div>
                {titleCity.map((item) =>
                    <div className="name" key={item.value} onClick={() => {
                        this.selCity(item)
                    }}>{item.label}</div>
                )}
            </div>
        );
    }
    // 
    renderCityIndex = () => {
        const { cityIndex, activeIndex } = this.state;
        return cityIndex.map((item, index) => {
            return (
                <li
                    key={item}
                    className="city-index-item"
                    onClick={
                        () => {
                            // 
                            // debugger
                            this.listRef.scrollToRow(index)
                            // this.setState({
                            //     activeIndex: index
                            // })
                        }
                    }
                >
                    <span className={activeIndex === index ? 'index-active' : ''}>
                        {this.formTitle(item, true)}
                    </span>
                </li >
            )
        })
    }
    // 
    onRowsRendered = ({ startIndex }) => {
        if (this.state.activeIndex !== startIndex) {
            this.setState({
                activeIndex: startIndex
            })
        }
    }
    render() {
        return (
            <div className="cityListBox">
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                ></NavBar>
                <AutoSizer>
                    {({ height, width }) => (
                        <List
                            ref={(ele) => {
                                this.listRef = ele
                            }}
                            scrollToAlignment="start"
                            onRowsRendered={this.onRowsRendered}
                            className="listBox"
                            height={height}
                            rowCount={this.state.cityIndex.length}
                            rowHeight={this.execHeight}
                            rowRenderer={this.rowRenderer}
                            width={width}
                        />
                    )}
                </AutoSizer>
                <ul className="city-index">
                    {this.renderCityIndex()}
                </ul>

            </div>
        )
    }
}
export default CityList;