import React from 'react'

import { Flex } from 'antd-mobile'
import PropTypes from 'prop-types'

import styles from './index.module.css'

function FilterFooter({ className, style, onCancel, onOk }) {
  return (
    <Flex style={style} className={[styles.root, className || ''].join(' ')}>
      {/*  */}
      <span onClick={onCancel} className={[styles.btn, styles.cancel].join(' ')}></span>

      {/*  */}
      <span onClick={onOk} className={[styles.btn, styles.ok].join(' ')}></span>
    </Flex>
  )
}

// props
FilterFooter.propTypes = {
  className: PropTypes.string,
  style: PropTypes.object
}

export default FilterFooter
