/*  */
import React, { Component } from 'react';
import { NavBar, Icon } from 'antd-mobile';
import { getCurCity } from '../../utils';

import styles from './index.module.css'
import { getMapData } from '../../utils/api/city';
import { getListByFilters } from '../../utils/api/house';
import { BASE_URL } from '../../utils/axios';
import HouseItem from '../../components/HouseItem'

class Map extends Component {
    state = {
        // 
        list: [],
        // 
        isShowList: false
    }

    componentDidMount() {
        // 
        this.initMap()
    }

    // 
    /**
     * 1. 
     * 2. 
     * 3. 
     */
    initMap = async () => {
        // BMapthis=>
        this.BMap = window.BMap;
        // 
        this.map = new this.BMap.Map("container");

        // =
        const { label, value } = await getCurCity();
        //      
        const myGeo = new this.BMap.Geocoder();
        //     
        myGeo.getPoint(null, async (point) => {
            if (point) {
                this.map.centerAndZoom(point, 11);
                // 
                // 
                this.map.addControl(new this.BMap.NavigationControl());
                // 
                this.map.addControl(new this.BMap.ScaleControl());

                // 
                this.renderOverlay(value)
            }
        },
            label);
        // 
        this.map.addEventListener('movestart', () => {
            if (this.state.isShowList) {
                this.setState({
                    isShowList: false
                })
            }
        })

    }

    // 1.  2.  
    /**
     * id: ID
     */
    renderOverlay = async (id) => {
        // TODO
        // 
        const { type, nextZoom } = this.getTypeAndZoom()

        const { status, data } = await getMapData(id);
        if (status === 200) {
            // =/
            data.forEach((item) => {
                // 
                this.createOverlays(type, nextZoom, item)

            })
        }
    }


    /**
     * /
     */
    createOverlays = (type, nextZoom, item) => {
        const { coord: { longitude, latitude }, label: cname, count, value } = item;
        // 1. htmlcss 2 
        let tpl, ecallback;
        // 
        if (type === 'circle') {
            tpl = `
      <div class="${styles.bubble}">
        <p class="${styles.bubbleName}">${cname}</p>
        <p>${count}</p>
      </div>
      `;
            ecallback = () => {
                // =ID=
                // 
                this.map.centerAndZoom(ipoint, nextZoom)
                // 
                this.renderOverlay(value)
                // 
                let id = setTimeout(() => {
                    clearTimeout(id);
                    this.map.clearOverlays()
                })
            }
        } else {
            // 
            tpl = `
            <div class="${styles.rect}">
            <span class="${styles.housename}">${cname}}</span>
            <span class="${styles.housenum}">${count}</span>
            <i class="${styles.arrow}"></i>
          </div>
      `;
            ecallback = (e) => {
                // console.log(e, cname)
                // 
                this.handlerHouseList(value)
                // 
                this.moveToCenter(e)
            }
        }
        // 
        const ipoint = new this.BMap.Point(longitude, latitude)
        // 
        //     
        const opts = {
            position: ipoint,    // 
            offset: new this.BMap.Size(0, 0)    //
        }
        // =
        const label = new this.BMap.Label(null, opts);  // 
        // setContenthtml
        label.setContent(
            tpl
        )
        // 
        label.setStyle({
            border: 0,
            background: 'transparent'
        });
        // 
        label.addEventListener('click', ecallback)
        // addOverlay=
        this.map.addOverlay(label);
    }

    // =
    getTypeAndZoom = () => {
        let type, nextZoom;
        // 
        const curZoom = this.map.getZoom()
        // 
        if (curZoom >= 10 && curZoom < 12) {
            type = 'circle';
            nextZoom = 13
        } else if (curZoom >= 12 && curZoom < 14) {
            type = 'circle';
            nextZoom = 15
        } else {
            // 
            type = 'square'
        }
        return {
            type, nextZoom
        }
    }

    // 
    handlerHouseList = async (id) => {
        const { status, data: { list } } = await getListByFilters(id);
        if (status === 200) {
            this.setState({
                // list: data,   data
                list,
                isShowList: true
            })
        }
    }

    // 
    renderHouseList = () => {
        return (
            <div
                className={[
                    styles.houseList,
                    this.state.isShowList ? styles.show : ''
                ].join(' ')}
            >
                <div className={styles.titleWrap}>
                    <h1 className={styles.listTitle}></h1>
                    <a className={styles.titleMore} href="/home/house">
                        
    </a>
                </div>

                <div className={styles.houseItems}>
                    {/*  */}
                    {
                        this.state.list.map(item => (
                            <HouseItem
                                onClick={() => this.props.history.push(`/detail/${item.houseCode}`)}
                                key={item.houseCode}
                                src={BASE_URL + item.houseImg}
                                title={item.title}
                                desc={item.desc}
                                tags={item.tags}
                                price={item.price}
                            />
                        ))
                    }
                </div>
            </div>
        )
    }
    // 
    moveToCenter = (e) => {
        // 
        const { clientX, clientY } = e.changedTouches[0];
        // (=y)
        let xc, yc;
        xc = window.innerWidth / 2;
        yc = (window.innerHeight - 330) / 2;
        // (x,y)
        let x = xc - clientX, y = yc - clientY;
        // 
        this.map.panBy(x, y);
    }
    render() {
        return (
            <div className={styles.mapBox}>
                {/*  */}
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.props.history.goBack()}
                ></NavBar>
                {/*  */}
                <div id="container"></div>
                {/*  */}
                {this.renderHouseList()}
            </div>
        );
    }
}

export default Map;