import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'

import { Link } from 'react-router-dom'

import styles from './index.module.css'
import { login } from '../../utils/api/user'
import { setLocalData, HJZFW_TOKEN } from '../../utils'
import { withFormik } from 'formik';

import * as yup from 'yup'; // for everything

// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

class Login extends Component {
  // 
  state = {
    username: '',
    password: ''
  }
  // 
  handerForm = (e) => {
    // console.log(e.target)
    // 
    this.setState({
      [e.target.name]: e.target.value
    })
  }
  // 
  // login = async (e) => {
  //   // 
  //   e.preventDefault()
  //   // =
  //   const { username, password } = this.state
  //   // console.log(username, password)
  //   // 

  //   // (test2,test2)
  //   let { data, description, status } = await login({ username, password })
  //   if (status === 200) {
  //     // 
  //     // 1. token
  //     setLocalData(HJZFW_TOKEN, data.token)
  //     // 2. 
  //     // this.props.history.push('/home/profile')
  //     console.log('bugger', this.props)
  //     if (this.props.location.backUrl) {
  //       this.props.history.replace(this.props.location.backUrl);
  //     } else {
  //       this.props.history.push('/home/profile')
  //     }
  //   } else {
  //     // 
  //     // 1.
  //     Toast.fail(description, 2)
  //     // 2. 
  //     this.setState({
  //       username: '',
  //       password: ''
  //     })
  //   }
  // }
  render() {
    const {
      values,
      errors,
      handleChange,
      handleSubmit,
    } = this.props;
    console.log(errors);
    // 
    // const { username, password } = this.state
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light">
          
        </NavBar>
        <WhiteSpace size="xl" />


        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                value={values.username}
                onChange={handleChange}
                className={styles.input}
                name="username"
                placeholder=""
              />
            </div>
            {/* 58 */}
            <div className={styles.error}>{errors.username}</div>
            <div className={styles.formItem}>
              <input
                value={values.password}
                onChange={handleChange}
                className={styles.input}
                name="password"
                type="password"
                placeholder=""
              />
            </div>
            {/* 512 */}
            <div className={styles.error}>{errors.password}</div>
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    )
  }
}

// withFormik
// 1. 
// 2. 
const MyLogin = withFormik({
  // 
  mapPropsToValues: () => ({ username: '', password: '' }),


  // 
  // Custom sync validation
  // validate: values => {
  //   const errors = {};

  //   if (!values.name) {
  //     errors.name = 'Required';
  //   }

  //   return errors;
  // },
  validationSchema: yup.object().shape({
    username: yup.string().required('').matches(REG_UNAME, '58'),
    password: yup.string().required('').matches(REG_PWD, '512')
  }),


  // 
  handleSubmit: async (values, { props, setValues }) => {
    console.log(values, props)
    // =
    const { username, password } = values

    // (test2,test2)
    let { data, description, status } = await login({ username, password })
    if (status === 200) {
      // 
      // 1. token
      setLocalData(HJZFW_TOKEN, data.token)
      // 2. 
      // props.history.push('/home/profile')
      if (props.location.backUrl) {
        props.history.replace(props.location.backUrl);
      } else {
        props.history.push('/home/profile')
      }
    } else {
      // 
      // 1.
      Toast.fail(description, 2)
      // 2. 
      setValues({ username: '', password: '' })
    }
  },

  displayName: 'BasicForm',
})(Login);
export default MyLogin
