import React from 'react'

import { Toast } from 'antd-mobile'

import Filter from './components/Filter'
// 
import styles from './index.module.css'
import { getCurCity } from '../../utils'
import { getListByFilters } from '../../utils/api/house'
import { AutoSizer, List, InfiniteLoader } from 'react-virtualized';
import HoustItem from '../../components/HouseItem'
import { BASE_URL } from '../../utils/axios'
import NoHouse from '../../components/NoHouse'

export default class HouseList extends React.Component {
    state = {
        // 
        list: [],
        // 
        count: 0
    }
    // 
    onFilter = (filters) => {
        console.log('', filters);
        this.filters = filters
        // 3. 
        this.getHouseList()
    }
    async componentDidMount() {
        // 1.Id
        let { value } = await getCurCity()
        this.cityId = value;
        // console.log('', this.cityId)
        // 3. 
        this.getHouseList()
    }
    // 2. 
    getHouseList = async () => {
        const { status, data: { list, count } } = await getListByFilters(this.cityId, this.filters)
        if (status === 200) {
            if (count > 0) {
                Toast.success(`${count}`, 2)
            }
            this.setState({
                list,
                count
            })
        }
    }

    // 
    isRowLoaded = ({ index }) => {
        const { list } = this.state;
        return !!list[index];
    }

    // 
    // fetch window
    loadMoreRows = ({ startIndex, stopIndex }) => {
        // return fetch(`path/to/api?startIndex=${startIndex}&stopIndex=${stopIndex}`)
        //     .then(response => {
        //         // Store response data in list...
        //     })
        return getListByFilters(this.cityId, this.filters, startIndex, stopIndex).then((res) => {
            console.log(res)
            // 
            if (res.status === 200) {
                this.setState({
                    count: res.data.count,
                    list: [...this.state.list, ...res.data.list]
                })
            }
        })
    }
    // 
    rowRenderer = ({
        key,
        index,
        isScrolling,
        isVisible,
        style,
    }) => {
        // row
        const { list } = this.state;
        const item = list[index];
        // item
        // if (!item) {
        //     return null
        // }
        if (!item) {
            return (
                <div style={style} key={key}>
                    <p className={styles.loading}></p>
                </div>
            )
        }
        // key
        item.src = `${BASE_URL}${item.houseImg}`
        return (
            <HoustItem onClick={() => {
                // 
                this.props.history.push(`/detail/${item.houseCode}`)
            }} {...item} key={key} style={style} />
        );
    }

    // 
    renderHouseList = () => {
        const { count } = this.state
        return count !== 0 ? <InfiniteLoader
            isRowLoaded={this.isRowLoaded}
            loadMoreRows={this.loadMoreRows}
            rowCount={this.state.count}
        >
            {({ onRowsRendered, registerChild }) => (
                <AutoSizer>
                    {({ height, width }) => (
                        <List
                            className={styles.houseList}
                            width={width}
                            height={height}
                            onRowsRendered={onRowsRendered}
                            ref={registerChild}
                            rowCount={this.state.count}
                            rowHeight={130}
                            rowRenderer={this.rowRenderer}
                        />
                    )}
                </AutoSizer>
            )
            }
        </InfiniteLoader > : <NoHouse>!</NoHouse>
    }
    render() {
        return (
            <div className={styles.root}>
                {/*  */}
                <Filter onFilter={this.onFilter} />
                {/*  */}
                {this.renderHouseList()}
            </div>
        )
    }
}
